import React from 'react';
import './App.css';
import Display from "./components/Display";
import Controls from "./components/Controls";

class App extends React.Component {
    state = {
        isVisibleOptions: false,
        maxVal: 5,
        minVal: 0,
        counter: 0
    }

    constructor() {
        super()
        this.app = JSON.parse(localStorage.getItem('app'));
        if (localStorage.getItem('app')) {
            this.setState({
                isVisibleOptions: this.app.isVisibleOptions,
                maxVal: this.app.maxVal,
                minVal: this.app.minVal,
                counter: this.app.counter
            })
        } else {
            this.setState({
                isVisibleOptions: false,
                maxVal: 5,
                minVal: 0,
                counter: 0
            })
        }
    }


    componentWillUpdate(nextProps, nextState) {
        localStorage.setItem('app', JSON.stringify(nextState));
    }

    changeMaxAndMinValue = (obj) => {
        this.setState({counter: obj.minVal})
        this.setState({maxVal: obj.maxVal, minVal: obj.minVal});
    }

    incrementCounter = () => {
        if (this.state.minVal === this.state.maxVal) {
            this.setState({error: true})
        } else {
            this.setState({counter: ++this.state.counter});
        }

    }
    refreshCounter = () => {
        this.setState({counter: 0, minVal: 0})
    }
    setOptions = (val) => {
        debugger
        this.setState({isVisibleOptions: val})
    }


    render() {
        return (
            <div className="App">
                <div className="Wrapper">
                    <Display counter={this.state.counter} error={this.state.error}
                             changeMaxAndMinValue={this.changeMaxAndMinValue} maxVal={this.state.maxVal}
                             minVal={this.state.minVal} setOptions={this.setOptions}
                             isVisibleOptions={this.state.isVisibleOptions}/>


                    <Controls counter={this.state.counter} error={this.state.error} maxVal={this.state.maxVal}
                              minVal={this.state.minVal} isVisibleOptions={this.state.isVisibleOptions}
                              setOptions={this.setOptions}
                              refreshCounter={this.refreshCounter}
                              incrementCounter={this.incrementCounter}/>
                </div>
            </div>
        );
    }

}

export default App;
